import React from "react";
import styled, { css } from "styled-components";
import Tag from "./Tag";

const StyledTags = styled.div`
  width: 60vw;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
`;

function Tags({ children, ...props }) {
  return (
    <div>
      <StyledTags {...props}>
        <Tag value="와인">와인🍷</Tag>
        <Tag value="맥주">맥주🍺</Tag>
        <Tag value="소주">소주🥛</Tag>
        <Tag value="칵테일">칵테일🍹</Tag>
        <Tag value="막걸리">막걸리🍶</Tag>
      </StyledTags>
    </div>
  );
}
export default Tags;
