import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import { useNavigate } from "react-router-dom";
import styled from "styled-components";
import Logo from "../../components/Logo/Logo";
import InputText from "../../components/Input/InputText";
import Select from "../../components/Seclect/Seclect";
import Button from "../../components/Button/Button";
import InsertReview from "../../components/Modal/InsertReview";
import Modal from "../../components/Modal/Modal";
const { kakao } = window;

const MapBtnWrap = styled.div`
  z-index: 999;
  position: fixed;
  bottom: 2vh;
  right: 2vw;
  width: 13vw;
`;

const MapHeadWrap = styled.div`
  z-index: 999;
  width: 50%;
  position: fixed;
  top: 2vh;
  left: 2vw;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  h2 {
  }
  form {
    width: 80%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    select {
      width: 28%;
    }
    input {
      width: 55%;
      :focus {
        outline: none;
      }
    }
    button {
      width: 14%;
    }
  }
`;

let positions = [
  {
    id: 1,
    content: "<div>test1</div>",
    Shop_id: "기유",
    Category: "소주",
    Img: "https://w.namu.la/s/2ca7b27652c2d5df35f48fbd57efa4be9854c5081f7adf98aca5f4996a716da697eb995227741edffa6ff8179b38fc01c9d21b651d8df664bf14871f055bf41f760976bf339281ba18710c3d60ccd41b3e623eb048b2ee377b0a87569d5d0d79",
    Review: "물의 호흡! 잔잔한 물결!",
    latlng: new kakao.maps.LatLng(37.472479737219366, 126.88452981876438),
    Value: "test1",
  },
  {
    id: 2,
    content: "<div>test2</div>",
    Shop_id: "렌고쿠",
    Category: "와인",
    Img: "https://blog.kakaocdn.net/dn/tEutU/btq5MvgjkCS/luqKAks5Yk0hNKKqaVSask/img.png",
    Review: "화염의 호흡! 연옥!",
    latlng: new kakao.maps.LatLng(37.47178177274328, 126.88485590268574),
    Value: "test2",
  },
  {
    id: 3,
    content: "<div>test3</div>",
    Shop_id: "젠이츠",
    Category: "맥주",
    Img: "https://pbs.twimg.com/media/FWbTk5dacAEnR-e?format=jpg&name=large",
    Review: "번개의 호흡!! 벽력일섬!",
    latlng: new kakao.maps.LatLng(37.47341347596826, 126.88575779553435),
    Value: "test3",
  },
  {
    id: 4,
    content: "<div>test4</div>",
    Shop_id: "탄지로",
    Category: "막걸리",
    Img: "https://w.namu.la/s/8e95df3580e6651b3b05c4423309ff6158484f4a7bc70c67901395d7004760eb2a7479a538120dda79f876b3b8ca63b9928b658ede9cb7770a5758ba781a0e454556a9fa1c3b15b6c51f46773c6074ef8e1340390ef930bd3eba0358e2d2d5ee9e92d4cd65a0ef4bcd1145b800388325",
    Review: "히노카미 카구라!!!",
    latlng: new kakao.maps.LatLng(37.47159627713495, 126.88636818758528),
    Value: "test4",
  },
];

let posData = [];

//--------------------Map--------------------------
const Map = () => {
  const [isOpen, setIsOpen] = useState(false);

  const navigate = useNavigate();
  function goHome() {
    navigate("/");
    // localStorage.clear();
  }

  function doSubmit(e) {
    let getInput = document.getElementById("map-search-bar").value;
    console.log(getInput);
    let getSelect = document.getElementById("select");
    console.log(getSelect.value);

    window.localStorage.setItem("searchValue", getInput);
    // e.preventDefault();
  }

  useEffect(() => {
    if (window.localStorage.getItem("searchValue")) {
      
      console.log("값있음");
      var mapContainer = document.getElementById("map"), // 지도를 표시할 div
        mapOption = {
          center: new kakao.maps.LatLng(37.566826, 126.9786567), // 지도의 중심좌표
          level: 3, // 지도의 확대 레벨
        };

      // 지도를 생성합니다
      var map = new kakao.maps.Map(mapContainer, mapOption);

      // 장소 검색 객체를 생성합니다
      var ps = new kakao.maps.services.Places();

      // 키워드로 장소를 검색합니다
      ps.keywordSearch(
        window.localStorage.getItem("searchValue"),
        placesSearchCB
      );

      // 키워드 검색 완료 시 호출되는 콜백함수 입니다
      function placesSearchCB(data, status, pagination) {
        if (status === kakao.maps.services.Status.OK) {
          // 검색된 장소 위치를 기준으로 지도 범위를 재설정하기위해
          // LatLngBounds 객체에 좌표를 추가합니다
          var bounds = new kakao.maps.LatLngBounds();

          for (var i = 0; i < 3; i++) {
            // displayMarker(data[i]);
            bounds.extend(new kakao.maps.LatLng(data[i].y, data[i].x));
            console.log(data[0].x, data[0].y);
          }
          // 검색된 장소 위치를 기준으로 지도 범위를 재설정합니다
          map.setBounds(bounds);
        }
      }
    }
    if (!window.localStorage.getItem("searchValue")) {
      // let lat = position.coords.latitude, // 위도
      // lon = position.coords.longitude; // 경도
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(Curposition);
      }
      function Curposition(pos) {
        // 위도
        let lat = pos.coords.latitude;
        // 경도
        let lng = pos.coords.longitude;
        // console.log(`위도 : ${lat} 경도 : ${lng}`);
        // 로컬 스토리지에 저장
        window.localStorage.setItem("lat", lat);
        window.localStorage.setItem("lng", lng);
      }

      // 현재 위치의 위도,경도
      var lat = parseFloat(localStorage.getItem("lat")); // 위도
      var lng = parseFloat(localStorage.getItem("lng")); // 경도

      // 중심 좌표
      var coords = new kakao.maps.LatLng(lat, lng);
      let mapContainer = document.getElementById("map"); // 지도를 표시할 div
      let mapOption = {
        center: new kakao.maps.LatLng(lat, lng), // 지도의 중심좌표
        level: 3, // 지도의 확대 레벨
      };
      const map = new kakao.maps.Map(mapContainer, mapOption);
      // console.log("클리어");
      map.setCenter(coords);
      // localStorage.clear();

      for (var i = 0; i < positions.length; i++) {
        let getPositionId = positions[i].id;
        // 마커를 생성합니다
        var marker = new kakao.maps.Marker({
          map: map, // 마커를 표시할 지도
          position: positions[i].latlng, // 마커의 위치
        });

        // 마커에 표시할 인포윈도우를 생성합니다
        var infowindow = new kakao.maps.InfoWindow({
          content: positions[i].Shop_id, // 인포윈도우에 표시할 내용
        });
        // 마커에 mouseover 이벤트와 mouseout 이벤트를 등록합니다
        // 이벤트 리스너로는 클로저를 만들어 등록합니다
        // for문에서 클로저를 만들어 주지 않으면 마지막 마커에만 이벤트가 등록됩니다
        kakao.maps.event.addListener(
          marker,
          "mouseover",
          makeOverListener(map, marker, infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "mouseout",
          makeOutListener(infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "click",
          makeClickListener(positions[i])
        );
      }

      // 인포윈도우를 표시하는 클로저를 만드는 함수입니다
      function makeOverListener(map, marker, infowindow) {
        return function () {
          infowindow.open(map, marker);
        };
      }

      // 인포윈도우를 닫는 클로저를 만드는 함수입니다
      function makeOutListener(infowindow) {
        return function () {
          infowindow.close();
        };
      }
      //인포윈도우 클릭 시 모달 창 열리는 함수
      function makeClickListener(positions) {
        return function () {
          console.log(positions.id, positions.Shop_id, positions.Review);
          posData = positions;
          console.log(posData);
          setIsOpen(true);
        };
      }
    }
  }, []);

  return (
    <>
      <MapHeadWrap>
        <Logo onClick={goHome}>Logo</Logo>
        <form
          className="search-form"
          onSubmit={doSubmit}
          autocomplete="off"
          enctype="multipart/form-data"
        >
          <Select id="select"></Select>
          <InputText
            className="text"
            place="검색어를 입력하세요"
            id="map-search-bar"
          ></InputText>
          <Button info type="submit" className="btn">
            검색
          </Button>
        </form>
      </MapHeadWrap>
      <div id="map" style={{ width: "100vw", height: "100vh" }}></div>
      {isOpen && (
        <Modal
          open={isOpen}
          positions={posData}
          onClose={() => {
            setIsOpen(false);
          }}
        ></Modal>
      )}
      <MapBtnWrap>
        <InsertReview></InsertReview>
      </MapBtnWrap>
    </>
  );
};
export default Map;
